body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.App {
    margin: 0 auto;
    max-width: 500px;
}
.question {
}

.correct {
    background-color: #93d993 !important;
}
.false {
    background-color: #db7272 !important;
}
.parsedText {
    img {
        max-height: 30vh;
        max-width: 100%;
    }
}
.answers {
    display: flex;
    align-items: center;

    img {
        max-height: 200px;
        max-width: 100%;
    }
}
.result {
    .count {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .correctCount {
        color: green;
        font-size: 2rem;
        margin-right: 0.5rem;
    }
    .falseCount {
        font-size: 2rem;
        margin-right: 0.5rem;
        color: red;
    }
    .percentage {
        display: flex;
        flex-direction: column;
        align-items: center;
        .percentageNumber {
            font-size: 4rem;
        }
        margin-bottom: 2rem;
    }
    .good {
        color: green;
    }
    .bad {
        color: red;
    }
    .restultQuestion {
        margin: 0.5rem 0;
    }
}
.MuiPaper-elevation3 {
    margin: 0.5rem 0;
    padding: 0.5rem;
}
h2 {
    margin: 0.25rem;
}
.questionNavi {
    display: flex;
    justify-content: flex-end;
    button {
        margin-left: 1rem;
    }
}
.fileUpload {
    margin: 1rem 0;
    .name {
        margin: 0.25rem;
    }
}
.settings {
    margin: 1rem;
}
